import { ModuleWithProviders }  from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { UserSuggestionComponent,
         LoginFormComponent,
         RegisterFormComponent,
         CanActivateViaAuthGuard
        } from './user/user.component';
import { PageNotFoundComponent } from './core/core.component';

const appRoutes : Routes = [
  {path: '', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./core/core.module')['CoreModule']);  });})},
  {path: 'user', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./user/user.module')['UserModule']);  });})},
  {path: 'product', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./product/product.module')['ProductModule']);  });})},
  {path: 'deal', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./deal/deal.module')['DealModule']);  });}), canActivate: [CanActivateViaAuthGuard]},
  {path: 'category', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./category/category.module')['CategoryModule']);  });})},
  {path: 'notification', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./notification/notification.module')['NotificationModule']);  });}), canActivate: [CanActivateViaAuthGuard]},
  {path: 'search', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./search/search.module')['SearchModule']);  });})},
  {path: '**', component:PageNotFoundComponent},
];

export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes,{useHash:true});



